clude select;\n  background-color: bisque;\n  p {\n    color: #fff;\n  }\n}\n\n.selected:hover {\n  transform: translate(0, -10px);\n  box-shadow: 0px 10px 20px $black-08;\n  transition: 0.2s;\n}\n\n.selected:active {\n  background-color: #fff;\n  transform: translate(0, -5px);\n  transition: 0.2s;\n}\n\n.logo {\n  width: 2rem;\n  height: 2rem;\n  object-fit: contain;\n  margin-right: 1rem;\n}\n"]}]);
